import React from "react";
import { ZwindowGapHeight, ZpageWrapper } from "zerod";
import { Row, Col } from "antd";
import HOC from "@/HOC/load-HOC";
const { AmdDocHOC,AshowDemoHOC } = HOC;
import doc from "zerod/components/ZwindowGapHeight/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		const MyScript = AshowDemoHOC(
			<ZpageWrapper
				pageHeader={{
					show: true,
					title: "",
					content: "",
				}}
			>
				<div>
					<Row gutter={16}>
						<Col md={10}>
							<ZwindowGapHeight querySelector=".z-panel" gap={20}>
								<div>
									<div className="z-panel">
										<div className="z-panel-heading">panel-heading</div>
										<div className="z-panel-body">panel-body</div>
									</div>
								</div>
							</ZwindowGapHeight>
						</Col>
						<Col md={14}>右边</Col>
					</Row>
				</div>
			</ZpageWrapper>,
			false,
		);
		return <MyScript />;
	},
});
